import React from 'react';
import classNames from 'classnames/bind';

import styles from './Controls.module.scss';

const cn = classNames.bind(styles);

export interface ControlsProps {
  /** Контроллы */
  children: React.ReactNode;

  /** Панель контролов шире области редактирования */
  widePanel?: boolean;

  /** ID для тестирования */
  dataTestId?: string;
}

/** Заголовок редактора со всеми элементами управления */
const Controls: React.FC<ControlsProps> = ({ widePanel, children, dataTestId }) => (
  <div className={ cn('controls', { 'controls--wide': widePanel }) } data-test-id={ dataTestId }>
    { children }
  </div>
);

Controls.displayName = 'Controls';

export default React.memo(Controls);
